body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.5;
}
.container-wrap{
  background-color: #272c34;
}
.container{
  max-width: 900px;
  margin: 0 auto;
  width: 100%;
  display: flex;
}
.bar{
  padding: 0 8px;
  flex-basis: 50%;
  max-width: 50%;
}

.card{
  width: 100%;
  background-color: #32363d;
  margin-bottom: 15px;
  color: whitesmoke;
  display: flex;
  align-items: flex-start;
}

.row {
  margin-top: 8px;
}

.thumbnail {
  vertical-align: middle;
  border-style: none;
}

.media-body {
  flex: 1;
  padding-left: 10px;
}

.genre-list {
  list-style-type: none;
  display: inline;
  padding-right: 5px;
  font-size: 12px;
  color: #67686a;
}

.movie-overview {
  font-size: 12px;
  color: #808692
}

.vote {
  font-size: 20px;
}

.out-of-10 {
  font-size: 12px;
}

.read-more {
  color: whitesmoke;
  text-decoration: none;
  padding-left: 5px;
}